import React from 'react';

function Lang(props) {
    return (
        <div className='language close'>
            <div className='drop'>
                <div className='icon'></div>
                <div className='drop-idcon'></div>
                <div className='current'>
                    {
                        props.lang === 'en' ? 'English' : props.lang === 'ru' ? '' : ''
                    }
                </div>
                <div className='down'>
                    <div style={{display: props.lang === 'en' ? 'none' : 'block'}} className={`lang ${props.data["en"].content === "" ? "no" : ""}`} value='en' onClick={() => props.setL('en')}>English</div>
                    <div style={{display: props.lang === 'ru' ? 'none' : 'block'}} className={`lang ${props.data["ru"].content === "" ? "no" : ""}`} value='ru' onClick={() => props.setL('ru')}></div>
                    <div style={{display: props.lang === 'uk' ? 'none' : 'block'}} className={`lang ${props.data["uk"].content === "" ? "no" : ""}`} value='uk' onClick={() => props.setL('uk')}></div>
                </div>
            </div>
        </div>
    );
}

export default Lang;
