import React from 'react';
import Chessboard from 'chessboardjsx';
import StockFish from './Stockfish';
import './Captcha.css';

function Captcha() {
    return (
        <div id='captcha'>
            <div className='instru'>
                <div className='ctions'>
                    <div className='try harder'>Complete the following challenge</div>
                    <div className='it is possible'>checkmate a computer</div>
                    <div className='you need to be better'>Click verify once the opponent can't move.</div>
                </div>
            </div>
            <div className='captcha'>
                <StockFish>
                    {({ position, onDrop }) => (
                        <Chessboard
                            id="stockfish"
                            position={position}
                            width={500}
                            onDrop={onDrop}
                            orientation="black"
                        />
                    )}
                </StockFish>
            </div>
            <div className='verifying'>
                <div className='left'>
                    <div className='refresh'></div>
                    <div className='listen'></div>
                    <div className='info'></div>
                </div>
                <button className='right'>
                    Verify
                </button>
            </div>
        </div>
    );
}

export default Captcha;
