import React from 'react';
import { Link } from 'react-router-dom';
import './Head.css';

function Head(props) {
  const isEdit = props.edit;
  return (
    <div className="head">
      <div className='menus'>
        <div className='decoration'/>
        <Link to='#'>
          <div className={`menu ${isEdit ? '' : 'active'}`}>
            Page
          </div>
        </Link>
        <Link to='#Edit'>
          <div className={`menu ${isEdit ? 'active' : ''}`}>
              Edit
          </div>
        </Link>
      </div>
    </div>
  );
}

export default Head;

