import React from 'react';
import ReactMarkdown from 'react-markdown';
import remarkGfm from 'remark-gfm';
import { Link } from 'react-router-dom';
import Lang from './Lang';
import './Content.css';

function Content(props) {
  if (props.edit) {
    return (
      <div className="content">
        <div className='top'>
          <textarea className='title' defaultValue={props.data[props.lang].title} style={{ height: "45px", width: "100%" }} />
          <Lang lang={props.lang} setL={props.setL} data={props.data} />
        </div>
        <div className='paragraph'>
          <textarea defaultValue={props.data[props.lang].content} style={{ height: "calc(100dvh - 215px)", width: "100%", background: "transparent" }} />
        </div>
      </div>
    );
  }
  return (
    <div className="content">
      <div className='top'>
        <div className='title'>{props.data[props.lang].title}</div>
        <Lang lang={props.lang} setL={props.setL} data={props.data}/>
      </div>
      <div className='paragraph'>
        <ReactMarkdown
          remarkPlugins={[remarkGfm]}
          components={{
            a: ({ node, ...props }) => <Link to={props.href} {...props} />
          }}
        >{props.data[props.lang].content}</ReactMarkdown>
      </div>
    </div>
  );
}

export default Content;
