import React, { useState, useEffect } from 'react';
import { BrowserRouter as Router, Route, Routes, useLocation } from 'react-router-dom';
import Content from './Content';
import Panel from './Panel';
import Head from './Head';
import './App.css';
import Captcha from './Captcha';
const PagesData = require('./pages.json');

function App() {
    const [lang, setLang] = useState(() => {
        return localStorage.getItem('lang') || 'en';
    });
    const [theme, setTheme] = useState(() => {
        return localStorage.getItem('theme') || 'light';
    });

    useEffect(() => {
        localStorage.setItem('theme', theme);
    }, [theme]);

    useEffect(() => {
        localStorage.setItem('lang', lang);
    }, [lang]);

    useEffect(() => {
        document.body.classList = theme;
    }, [theme]);

    const toggleTheme = () => {
        const newTheme = theme === 'light' ? 'dark' : 'light';
        setTheme(newTheme);
    };

    const toggleLang = (lang) => {
        setLang(lang);
    }

    const location = useLocation();
    const currentPath = location.pathname;
    const currentHash = location.hash;

    const [pageData, setPageData] = useState(null);
    const [edit, setEdit] = useState(false);
    useEffect(() => {
        if (currentHash && currentHash.startsWith('#Edit')) {
            setEdit(true);
        } else {
            setEdit(false);
        }
        const requestOptions = {
            method: 'POST',
            headers: {
                'Content-Type': 'application/json',
                'Access-Control-Request-Headers': '*',
                'api-key': 'V3IrJhbA2Ji1L9BpVrVysSbewn2Lq9QTCPxJQXlrHJaJLKJJEhUPm3wgKuS9Rw9E'
            },
            body: JSON.stringify({
                collection: "pages",
                database: "minecraft_wiki",
                dataSource: "Minecraft",
                filter: { "path": currentPath }
            })
        };
        const proxyUrl = 'https://corsproxy.io/?'; // Replace with your proxy server URL
        fetch(`${proxyUrl}https://eu-central-1.aws.data.mongodb-api.com/app/data-akegaoi/endpoint/data/v1/action/findOne`, requestOptions)
            .then(response => response.json())
            .then(data => {
                if (!data.document) {
                    setPageData(PagesData["none"]);
                    return;
                } else { 
                    if (!data.document[lang]) {
                        setPageData(PagesData["none"]);
                    } else {
                        setPageData(data.document);
                    }
                }
                document.title = data.document[lang].title;
                import (`../public/themes/${data.document.theme || 'mc'}.css`);
            })
            .catch(error => {
                console.error("Error:", error);
            });

    }, [currentPath, currentHash, lang]);
       

    return (
        <div className="App">
            <div className="account">
                <div className='selection'>
                    <button className="them" onClick={toggleTheme}>Toggle Theme</button>
                    <div className="login">Login</div>
                </div>
            </div>
            {pageData && <Panel data={pageData} lang={lang}/>}
            {pageData && <Head data={pageData} edit={edit} lang={lang}/>}
            {pageData && <Content data={pageData} edit={edit} lang={lang} setL={toggleLang}/>}
            <Captcha />
        </div>
    );
}

export default function AppWrapper() {
    return (
        <Router>
            <Routes>
                <Route path="/*" element={<App />} />
            </Routes>
        </Router>
    );
}
