import React from 'react';
import { Link } from 'react-router-dom';
import './Panel.css';

function Panel(props) {
  function toggle(event) {
    const parent = event.currentTarget.closest('.selection');
    if (parent.classList.contains('open')) {
      parent.classList.remove('open');
      parent.classList.add('close');
    } else if (parent.classList.contains('close')) {
      parent.classList.remove('close');
      parent.classList.add('open');
    }
  }
  function togglePanel() {
    const parent = document.querySelector('.panel');
    if (parent.classList.contains('open')) {
      parent.classList.remove('open');
      parent.classList.add('close');
    } else if (parent.classList.contains('close')) {
      parent.classList.remove('close');
      parent.classList.add('open');
    }
  }

  const data = props.data[props.lang].panel;
  let index = 0;
  const selections = [];

  while (index < data.length) {
    if (data[index].type === 'list') {
      const items = data[index].list.map((item) => (
        <Link className="item" to={item.url}>{item.text}</Link>
      ));

      selections.push(
        <div className="selection">
          <div className="navigation">
            <div className="list">
              {items}
            </div>
          </div>
        </div>
      );
      index += 1;
    } else if (data[index].type === 'title') {
      selections.push(
        <div className="selection open">
          <div className="navigation">
            <div className="title" onClick={toggle}>
              <div className="drop-icon"></div>
              <span>{data[index].content}</span>
            </div>
          </div>
          <div className="navigation">
            <div className="list">
              {data[index + 1].list.map((item) => (
                <Link className="item" to={item.url}>{item.text}</Link>
              ))}
            </div>
          </div>
        </div>
      );
      index += 2;
    } else {
      index += 1;
    }
  }

  return (
    <div className="panel close">
      <div id='panel-c' onClick={togglePanel}>M</div>
      <Link className="logo" to="/"/>
      {selections}
    </div>
  );
}

export default Panel;